import React, { Component } from 'react';
import Headbar from './Components/headbar';
import Content from './Components/content';
import logo from './logo.svg';
import styled from 'styled-components';
import { connect } from 'react-redux';

const AppContainer = styled.div`
  position: relative;
  background:#333;
  width: ${ window.innerWidth>960 ? 960 : window.innerWidth }px;
  min-hegiht: ${ props => props.onresize ? window.innerHeight : window.innerHeight};
  left: ${ window.innerWidth>960 ? (window.innerWidth-960)/2 : 0 }px;
`

let curWidth = window.innerWidth;
let curHeight = window.innerHeight;
let boo = false;

export default class App extends Component {

  constructor(){
    super();
    window.onresize = () => {
      boo = true;
    }
  }
  render(){
    return (
        <AppContainer onresize={ boo ? true : false}>
          <Headbar />
          <Content />
        </AppContainer>
    )
  }
}



