import React, { Component } from 'react';
import styled from 'styled-components';
import CopyIconBtn from 'react-icons/lib/md/content-copy';
import copy from 'copy-to-clipboard';
import TickIcon from 'react-icons/lib/fa/check-circle-o';

const Title = styled.p`
	font-size: 18px;
	margin: 10px 20px 5px 40px;
		margin-left: ${ window.innerWidth > 400 ? 40 : 30 }px !important;
	font-weight: bold;
	font-style: oblique;
`
const SubTitle = styled.span`
	font-size: 18px;
	margin: 10px 20px 5px 40px;
	margin-left: ${ window.innerWidth > 400 ? 40 : 30 }px !important;
	font-weight: bold;
`
const Period = styled.span`
	font-size: 14px;
	margin: ${ window.innerWidth>450 ? '10px 20px 5px 180px' : '15px 20px 5px 40px' };
	margin-left: ${ window.innerWidth > 400 ? 40 : 30 }px !important;
`
const DescriptionField = styled.div`
	margin: ${ window.innerWidth>450 ? '15px 20px 5px 60px' : '15px 20px 5px 40px' };
	margin-left: ${ window.innerWidth > 400 ? 40 : 30 }px !important;
	background: #444;
	width: ${ window.innerWidth>450 ? 450 : window.innerWidth*0.8 }px;
	padding: 10px 0px 10px 0px;
	border-radius: 10px;
`
const List = styled.ul`
	margin: 5px;
	padding-left: 10px;
	list-style-type:none;
`
const Item = styled.li`
	font-size: 14px;
	margin: 5px;
`
const ResponsiveNextLine = styled.span`
  display: ${ window.innerWidth > 450 ? 'inline' : 'block' }
  &:after {
    content: ' ';
  }
`
const WorkContainer = styled.div`
	min-height: 750px;
`

const Tick = styled(TickIcon)`
	display: ${ window.innerWidth > 450 ? 'inline' : 'none' };
`


const workData = [
	{
		company: (() => {return window.innerWidth > 450 ? 'Hutchison Telecommunications Hong Kong Limited' : 'Hutchison Telecom HK Ltd.'})() ,
		title: 'System Engineer',
		period: 'April 2018 to Current',
		description: [
			'User Experience Design', 'Web based system development',
			'System integration', 'Test case preparation', 'Web server monitoring',
			'Responsible for system update'
		]
	},
	{
		company: 'Nexgen Packaging Limited',
		title: 'IT Support',
		period: 'February 2017 to April 2018',
		description: [
		  'Web based system & mobile device support', 'Troubleshooting', 'Project coordination',
		  'Providing business requirement document', 'Drafting prototype',
		  'Weekly conversation with Indian deverlopers', 'Experienced on system migration ( Cloud & Version update )'
		]
	},
];

export default class Work extends Component {
	render(){
		return (
		<WorkContainer>
				{ workData.map( curData => {
					console.log(curData.company);
					return (
						<div>
							<Title> {curData.company} </Title>
							<SubTitle> {curData.title} </SubTitle>
							<ResponsiveNextLine />
							<Period> {curData.period} </Period>
							<DescriptionField>
								<List>
									{curData.description.map( curDpt => {
										return (
											<Item> <Tick/> {curDpt} </Item>
										)
									})}
								</List>
							</DescriptionField>
							<br/>
						</div>
					)
				} )}
			</WorkContainer>
		)
	}
}



